import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Popper from '@mui/material/Popper';
import PopupState, { bindToggle, bindPopper } from 'material-ui-popup-state';
import Fade from '@mui/material/Fade';
import Paper from '@mui/material/Paper';
import styles from './profileMenu.module.scss'
import { getData } from '../FetchFunctions/fetchFunctionsAdmin/FetchFunctionsAdmin';
import { useNavigate } from 'react-router-dom';
import { TaskContext } from '../../components/Context/TaskContext';
import { useContext } from 'react';
import Avatar from 'react-avatar';

export default function PopperPopupState(): JSX.Element {
  const { userLogout } = useContext(TaskContext);
  const { auth } = useContext(TaskContext);
  let navigate = useNavigate() ;
 
   function nvtopages() :void {
  navigate('/');
}
function nvtoProfile() :void {
  navigate('/Profile');
}
  const logOut = () : void =>{
    getData('/api/v1/auth/logout')
    nvtopages();
    userLogout?.();
  }
  
  return (
    <PopupState variant="popper" popupId="demo-popup-popper">
      {popupState => (
        <div>
          <Button  {...bindToggle(popupState)}>
          <span className={styles.span}>Profile</span>
          <Avatar size="70" className={styles.avatar} 
            name={auth}
        /> 
        
          </Button>
          <Popper {...bindPopper(popupState)} transition>
            {({ TransitionProps }) => (
              <Fade {...TransitionProps} timeout={350}>
                <Paper>
                  <Typography sx={{ p: 2 }} onClick={nvtoProfile} bgcolor="grey" color="gold">
                  <div className={styles.typography}>
                   Edit Your Profile
                   </div>
                  </Typography>
                  <Typography onClick={logOut} sx={{ p: 2 }} bgcolor="grey" color="gold">
                  <div className={styles.typography}>
                   Log Out
                   </div>
                  </Typography>
                </Paper>
              </Fade>
            )}
          </Popper>
        </div>
      )}
    </PopupState>
  );
}
