import { useNavigate } from 'react-router-dom';
import styles from './administration.module.scss';
import PopperPopupState from './ProfileMenu/PorfileMenu';


const Administration : React.FC = () => {


  let navToAdminPages = useNavigate();

  function nvtopages(nav :string) {
    navToAdminPages(nav);
  }

  return (
    <div className={styles.administration}>
      <div className={styles.upperContent}>
      <h1>Admin Panel</h1>
        <span className={styles.profPopUp} >
      <PopperPopupState />
      </span> 
     
      </div>
      <div className={styles.administrationContent}>
      <div className={styles.adminBoxes} onClick={() => nvtopages('fileManager')}>
          <span>
            File Manager
          </span>
          <div className={styles.textVideoCarouselEffect} />
        </div>

        

       

        <div className={styles.adminBoxes} onClick={() => nvtopages('Movies')}>
          <span>
            Movies
          </span>
          <div className={styles.textVideoCarouselEffect} />
        </div>

        <div  className={styles.adminBoxes}onClick={() => nvtopages('Users')}>
        <span>
        Users  
          </span>
          <div className={styles.textVideoCarouselEffect} />
        </div>
      </div>
    </div>
  );
};
export default Administration;
